@font-face {
  font-family: "Lab Grotesque";
  font-style: normal;
  font-weight: 100;
  src: local("Lab Grotesque"), url("../fonts/LabGrotesque-Thin.woff2") format("woff2"), url("../fonts/LabGrotesque-Thin.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lab Grotesque";
  font-style: normal;
  font-weight: 300;
  src: local("Lab Grotesque"), url("../fonts/LabGrotesque-Light.woff2") format("woff2"), url("../fonts/LabGrotesque-Light.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lab Grotesque";
  font-style: normal;
  font-weight: 400;
  src: local("Lab Grotesque"), url("../fonts/LabGrotesque-Regular.woff2") format("woff2"), url("../fonts/LabGrotesque-Regular.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Lab Grotesque";
  font-style: normal;
  font-weight: 500;
  src: local("Lab Grotesque"), url("../fonts/LabGrotesque-Medium.woff2") format("woff2"), url("../fonts/LabGrotesque-Medium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Scania Sans CY";
  font-style: normal;
  font-weight: 700;
  src: local("Scania Sans CY"), url("../fonts/ScaniaSansCY-Bold.woff2") format("woff2"), url("../fonts/ScaniaSansCY-Bold.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Scania Sans CY Headline";
  font-style: normal;
  font-weight: 400;
  src: local("Scania Sans CY Headline"), url("../fonts/ScaniaSansCYHeadline-Regular.woff2") format("woff2"), url("../fonts/ScaniaSansCYHeadline-Regular.woff") format("woff");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  color: #ffffff;
  font-family: "Lab Grotesque", "Arial", sans-serif;
  font-size: 14px;
  line-height: 1.57;
  font-weight: 400;
}

body {
  display: flex;
  flex-direction: column;
  overflow-x: hidden;
  width: 100%;
  height: 100%;
  background-color: #141414;
  background-image: url("../img/background/main.jpg");
  background-repeat: repeat;
}

main {
  flex-grow: 1;
}

a {
  color: #ffffff;
  text-decoration: none;
  transition: color 0.3s ease;
}
@media (hover: hover) {
  a:hover:not(.focus-visible) {
    color: #ffcf7a;
  }
}
a:active {
  color: #ffcf7a;
}
a:focus-visible {
  outline: 2px solid #ffcf7a;
  outline-offset: 2px;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  min-width: 280px;
  max-width: 1290px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.reset-list {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style-type: none;
}

.navigation-opened {
  overflow: hidden;
}

.logo {
  display: inline-flex;
  align-items: center;
}

.logo__main {
  min-width: 140px;
  color: #ffffff;
  letter-spacing: 0.1em;
  user-select: none;
  font-family: "Scania Sans CY Headline", "Arial", sans-serif;
  font-size: 20px;
  line-height: 3.5;
  font-weight: 400;
}
@media (max-width: 1199px) {
  .logo__main {
    font-size: 18px;
  }
}

.logo--footer .logo__main {
  min-width: 98px;
  font-size: 14px;
  line-height: 5;
}

.logo__additional {
  max-width: 99px;
  margin-left: 39px;
  color: #6d6d6d;
  letter-spacing: 0.05em;
  font-family: "Lab Grotesque", "Arial", sans-serif;
  font-size: 12px;
  line-height: 1.5;
  font-weight: 400;
}
@media (max-width: 1199px) {
  .logo__additional {
    display: none;
  }
}

.button {
  position: relative;
  display: inline-flex;
  overflow: hidden;
  padding: 10px 25px 11px 25px;
  font-family: "Lab Grotesque", "Arial", sans-serif;
  font-size: 18px;
  line-height: 1.67;
  font-weight: 500;
  color: #121212;
  text-decoration: none;
  background-color: #eccf94;
  border: none;
  cursor: pointer;
  user-select: none;
  transition: all 0.3s ease;
}
.button::before, .button::after {
  position: absolute;
  z-index: 1;
  width: 65%;
  height: 124px;
  content: "";
}
.button::before {
  bottom: -81px;
  left: -18px;
  background-image: linear-gradient(270.21deg, rgba(249, 139, 77, 0.8) 18.08%, rgba(255, 103, 82, 0.8) 68.72%);
  filter: blur(25px);
}
.button::after {
  top: -81px;
  right: -18px;
  background-image: linear-gradient(270.21deg, rgba(255, 217, 196, 0.8) 18.08%, rgba(255, 255, 255, 0.8) 68.72%);
  filter: blur(25px);
  transform: matrix(-1, 0, 0, 1, 0, 0);
}
.button--back-call {
  width: 50px;
  min-width: 50px;
  height: 50px;
  min-height: 50px;
  padding: 0;
  border-radius: 2px;
}
@media (hover: hover) {
  .button:hover:not(.focus-visible) {
    color: #121212;
    opacity: 0.8;
  }
}
.button:active {
  opacity: 0.6;
}
.button:focus-visible {
  outline: 2px solid #ffcf7a;
  outline-offset: 2px;
}
.button--bordered {
  overflow: visible;
  padding: 14px 25px 16px 25px;
  color: #ffffff;
  background-color: transparent;
  background-image: none;
  border: 1px solid #ceb787;
  border-radius: 2px;
}
.button--bordered::before, .button--bordered::after {
  display: none;
}
@media (hover: hover) {
  .button--bordered:hover:not(.focus-visible) {
    color: #ffcf7a;
    border-color: #ffcf7a;
    opacity: 1;
  }
}
@media (max-width: 1199px) {
  .button {
    font-size: 16px;
  }
}

.button__inner {
  position: relative;
  z-index: 2;
}

.button--back-call .button__inner {
  display: block;
  width: 18px;
  height: 18px;
  margin: auto;
  background-image: url("../img/svg/phone.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.title--primary {
  font-family: "Lab Grotesque", "Arial", sans-serif;
  font-size: 64px;
  line-height: 1.25;
  font-weight: 250;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: transparent;
  background-clip: text;
  background-image: linear-gradient(215.43deg, #ffffff 49.37%, rgba(255, 255, 255, 0) 105%);
}
.title--secondary {
  font-family: "Lab Grotesque", "Arial", sans-serif;
  font-size: 40px;
  line-height: 1.5;
  font-weight: 250;
  text-transform: uppercase;
  letter-spacing: 0.07em;
}
@media (max-width: 1199px) {
  .title--primary {
    font-size: 54px;
  }
  .title--secondary {
    font-size: 34px;
  }
}
@media (max-width: 767px) {
  .title--primary {
    font-size: 29px;
    letter-spacing: 0.03em;
  }
  .title--secondary {
    font-size: 26px;
  }
}

.label {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 151px;
  height: 151px;
  padding: 20px;
  font-family: "Scania Sans CY", "Arial", sans-serif;
  color: #000000;
  text-align: center;
  background-image: radial-gradient(100% 65.98% at 100% 73.18%, #ffffff 0.31%, #ffe2a4 59.23%, #ffd47c 100%);
  border-radius: 50%;
}
@media (max-width: 1199px) {
  .label {
    width: 131px;
    height: 131px;
    padding: 15px;
  }
}
@media (max-width: 767px) {
  .label {
    width: 100px;
    height: 100px;
    padding: 12px;
  }
}

.label__title {
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  font-weight: 700;
}
@media (max-width: 767px) {
  .label__title {
    margin-bottom: 0;
  }
}

.label__title-main {
  font-size: 36px;
  line-height: 1;
}
@media (max-width: 1199px) {
  .label__title-main {
    font-size: 30px;
  }
}
@media (max-width: 767px) {
  .label__title-main {
    font-size: 24px;
  }
}

.label__title-additional {
  font-size: 16px;
  line-height: 1.2;
}
@media (max-width: 767px) {
  .label__title-additional {
    font-size: 12px;
  }
}

.label__additional {
  padding: 0 10px;
  font-size: 10px;
  line-height: 1.4;
  font-weight: 400;
}
@media (max-width: 1199px) {
  .label__additional {
    padding: 0 5px;
  }
}
@media (max-width: 767px) {
  .label__additional {
    padding: 0;
    font-size: 9px;
  }
}

.navigation {
  display: flex;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .navigation {
    flex-direction: column;
  }
}

.navigation__item {
  padding: 4px 0;
}
.navigation__item:not(:last-child) {
  margin-right: 45px;
}
@media (max-width: 1199px) {
  .navigation__item:not(:last-child) {
    margin-right: 30px;
  }
}
@media (max-width: 767px) {
  .navigation__item:not(:last-child) {
    margin-right: 0;
    margin-bottom: 15px;
  }
}

.navigation__link {
  font-size: 13px;
  color: #949494;
  text-transform: uppercase;
  letter-spacing: 0.05em;
}

.slider {
  position: relative;
  width: 100%;
  padding-bottom: 60.4%;
  background-color: #121212;
}

.slider__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider__list {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  height: 100%;
}

.slider__item {
  width: 37vw;
  height: 24vw;
}

.slider__controls {
  position: absolute;
  bottom: 40px;
  left: 45px;
  display: flex;
}

.slider__control {
  width: 18px;
  height: 18px;
  background-color: transparent;
  background-image: url("../img/svg/arrow-left.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
}
.slider__control--prev {
  margin-right: 16px;
}
.slider__control--next {
  transform: rotate(180deg);
}
@media (hover: hover) {
  .slider__control:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.slider__control:focus-visible {
  outline: 2px solid #ffcf7a;
  outline-offset: 2px;
}

.slider-with-pagination {
  position: relative;
  width: 327px;
  height: 77px;
  background-color: transparent;
}
@media (max-width: 767px) {
  .slider-with-pagination {
    width: 280px;
    height: 66px;
  }
}

.slider-with-pagination__control {
  position: absolute;
  top: 20px;
  width: 18px;
  height: 18px;
  background-color: transparent;
  background-image: url("../img/svg/arrow-left.svg");
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
.slider-with-pagination__control--prev {
  left: 0;
}
.slider-with-pagination__control--next {
  right: 0;
  transform: rotate(180deg);
}
@media (max-width: 767px) {
  .slider-with-pagination__control {
    top: 15px;
  }
}
@media (hover: hover) {
  .slider-with-pagination__control:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.slider-with-pagination__control:focus-visible {
  outline: 2px solid #ffffff;
  outline-offset: 2px;
}

.slider-with-pagination__wrapper {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  overflow: hidden;
  width: 247px;
  max-width: 247px;
  height: 59px;
  margin: 0 auto;
}
@media (max-width: 767px) {
  .slider-with-pagination__wrapper {
    width: 200px;
    max-width: 200px;
    height: 48px;
  }
}

.slider-with-pagination__items {
  display: flex;
}

.slider-with-pagination__image {
  width: 247px;
  min-width: 247px;
  height: 59px;
}
@media (max-width: 767px) {
  .slider-with-pagination__image {
    width: 200px;
    min-width: 200px;
    height: 48px;
  }
}

.slider-with-pagination__pagination {
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.slider-with-pagination__pagination-item {
  width: 9px;
  height: 1px;
  background-color: #464239;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
}
.slider-with-pagination__pagination-item--current {
  background-color: #ffcf7a;
}
@media (hover: hover) {
  .slider-with-pagination__pagination-item:hover:not(.focus-visible) {
    background-color: #ffcf7a;
  }
}
.slider-with-pagination__pagination-item:focus-visible {
  outline: 1px solid #ffffff;
}

.form__checkbox-label {
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;
}

.form__checkbox-control {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  font-size: 16px;
  color: #121212;
  border: 1px solid #ceb787;
  border-radius: 2px;
  transition: all 0.3s ease;
}

.form__checkbox-label:hover .form__checkbox-control {
  opacity: 0.6;
}

.form__checkbox:checked + .form__checkbox-control {
  background-color: #ceb787;
}
.form__checkbox:checked + .form__checkbox-control::after {
  content: "+";
}

.form__checkbox:focus-visible + .form__checkbox-control {
  outline: 2px solid #ffcf7a;
  outline-offset: 2px;
}

.form__range {
  display: flex;
  align-items: center;
}

.form__range-label {
  font-weight: 500;
  font-size: 18px;
  line-height: 3;
}

.form__range-control {
  display: block;
  margin-left: 40px;
  margin-right: 40px;
  width: 100%;
  max-width: 441px;
  height: 1px;
  -webkit-appearance: none;
  /* stylelint-disable-line */
  -moz-appearance: none;
  /* stylelint-disable-line */
  appearance: none;
}
.form__range-control::-moz-range-track {
  width: 100%;
  height: 100%;
  background-color: #646464;
  border-radius: 1px;
  cursor: pointer;
}
.form__range-control::-webkit-slider-runnable-track {
  width: 100%;
  height: 100%;
  background-color: #646464;
  border-radius: 1px;
  cursor: pointer;
}
.form__range-control::-moz-range-thumb {
  width: 30px;
  height: 30px;
  background-color: #ffcf7a;
  border: none;
  border-radius: 2px;
  cursor: pointer;
}
.form__range-control::-webkit-slider-thumb {
  width: 30px;
  height: 30px;
  background-color: #ffcf7a;
  border: none;
  border-radius: 2px;
  cursor: pointer;
  -webkit-appearance: none;
  /* stylelint-disable-line */
}
.form__range-control:focus-visible {
  outline: 2px solid #ffcf7a;
  outline-offset: 2px;
}
@media (max-width: 1199px) {
  .form__range-control {
    margin-left: 25px;
    margin-right: 25px;
  }
  .form__range-control::-moz-range-thumb {
    width: 20px;
    height: 20px;
  }
  .form__range-control::-webkit-slider-thumb {
    width: 20px;
    height: 20px;
  }
}

