  background-color: #121212;
  border-top: 1px solid #232323;
}
@media (max-width: 1199px) {
  .footer {
    padding: 15px 40px 18px;
  }
}
@media (max-width: 767px) {
  .footer {
    padding: 15px 20px 18px;
  }
}

.footer__wrapper {
  display: flex;
  align-items: center;
  padding: 0 48px;
}
@media (max-width: 1199px) {
  .footer__wrapper {
    padding: 0;
  }
}
@media (max-width: 767px) {
  .footer__wrapper {
    flex-direction: column;
