}
@media (hover: hover) {
  .footer__agreement:hover:not(.focus-visible) {
    color: #6d6d6d;
  }
}

.promo {
  padding: 20px 45px 183px;
}
@media (max-width: 1199px) {
  .promo {
    padding: 20px 40px 93px;
  }
}
@media (max-width: 767px) {
  .promo {
    padding: 20px 20px 23px;
  }
}

.promo__wrapper {
  position: relative;
  padding: 0 48px;
}
@media (max-width: 1199px) {
  .promo__wrapper {
    padding: 0;
  }
}
@media (max-width: 767px) {
