.promo__features-list {
  position: relative;
  z-index: 2;
  display: grid;
  grid-template-columns: repeat(3, 120px);
  grid-gap: 60px;
}
@media (max-width: 1199px) {
  .promo__features-list {
    grid-gap: 40px;
  }
}
@media (max-width: 767px) {
